.tb_shop_btn_wrap{
       display: block;
       text-align: center;
       max-width: 100%;
       padding: 15px;
}
.tb_shop_btn {
       border-radius: 0;
       display: inline-block;
       font-size: 12px;
       max-width: 100%;
       overflow: hidden;
       padding: 10px 20px;
       text-decoration: none;
       text-overflow: ellipsis;
       white-space: nowrap;
       transition: all .5s ease-in-out;
}

.tb_shop_btn_light{
       background-color: transparent;
       border: 1px solid #fff;
       color: #fff;
}

.tb_shop_btn_light:hover{
       background-color: #fff;
       border: 1px solid #fff;
       color: #000;
}

.tb_shop_btn_dark{
       background-color: #000;
       border: 1px solid #000;
       color: #fff;
}

.tb_shop_btn_dark:hover{
       background-color: #000;
       border: 1px solid #000;
       color: #fff;
}