.tb_rtp_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
}

.tb_rtp_post_slider{
    width: 100%;
    overflow: hidden;
}

.tb_rtp_post_wrapper{
    width: 100%;
    padding: 20px 10px;
    overflow: hidden;
    height: 100%;
}

.tb_rtp_fixed_height .tb_rtp_post_wrapper, 
.tb_rtp_fixed_height .tb_rtp_post_in{
    height: 100%;
}

.tb_rtp_post_in {
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: var(--tb-border-radius);
    overflow: hidden;
    cursor: pointer;
    height: 100%;
    transition: box-shadow .3s ease-in, transform .2s ease-in;
    //box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08)
}

.tb_rtp_post_in_animate:hover{
    transform: scale(1.04);
    transition: box-shadow .3s ease-in, transform .3s ease-in;
}
.tb_rtp_post_media_wrapp{
    position: relative;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
}
.tb_rtp_image{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
}

.tb_rtp_social_ico_ {
    font-size: 22px;
    color:#fff;
    margin: 15px 0;
}

.tb_rtp_play_ico {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 7rem;
    color: #fff;
    opacity: 0;
    transition-property: opacity;
    transition-timing-function: ease-in;
    transition-duration: .3s;
    transition-delay: .5s;
}

.tb_rtp_post_in:hover .tb_rtp_play_ico{
    opacity: .5;
    transition-property: opacity;
    transition-timing-function: ease-in;
    transition-duration: .3s;
}

.tb_rtp_image_wrap_, .tb_rtp_video_wrap_{
    padding-bottom: 178%;
    position: relative;
    background-color: rgba(0,0,0,.03);
}
.tb_rtp_image, .tb_rtp_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_rtp_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_rtp_video iframe{
    pointer-events: none;
}

.tb_rtp_post_media_wrapp{
    position: relative;
}

.tb_rtp_post_media_wrapp .fb_iframe_widget_fluid{
    width: 100%;
    display: flex;
    align-items: center;
}

@media only screen and (min-width: 991px)  {
    .tb_rtp_post_in:hover .tb_rtp_post_details,
    .tb_rtp_post_in:hover .tb_rtp_post_overlay {
        opacity: 1;
        visibility: visible;
    }
    
    .tb_rtp_post_animate:hover .tb_rtp_image {
        -webkit-transform: scale(1.07);
        transform: scale(1.07);
    }

    .tb_rtp_post_in:hover{
        transform: scale(1.025);
    }

    .tb_rtp_post_in:hover .tb_rtp_post_details{
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }

    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(0);
        }
    }

    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }
        100% {
            transform: translateY(0);
        }
    }

}

.tb_rtp_wrap{
    padding:15px;
    border: 1px solid rgba(0, 0, 0, .1);
    border-top: 1px solid transparent;
    border-bottom-right-radius: var(--tb-border-radius);
    border-bottom-left-radius: var(--tb-border-radius);
    z-index: 1;
    min-height: 135px;
    max-height: 160px;
}

.tb_rtp_wrap_in{
    position: relative;
    margin-top:calc(-30px + -5%);
}

.tb_rtp_media{
    width: 30%;
    max-width: 50px;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, .1);
    overflow: hidden;
    margin-bottom: 10px;
    background-color: #fff;
    //box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08)
}
.tb_rtp_img_wrap, .tb_rtp_a_img{
    position: relative;
    height: 100%;
    width: 100%;
    padding-bottom: 100%;
}
.tb_rtp_img, .tb_rtp_a_img__{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
}
.tb_rtp_title_w {
    min-height: 38px;  
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.tb_rtp_title, .tb_rtp_authorname{
    font-family: var(--tb-product-font);
    font-size: var(--tb-product-size);
    color: var(--tb-product-color);
    font-weight: var(--tb-product-varient);
    width: 100%;
    word-wrap: break-word;
}

.tb_rtp_title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}

.tb_rtp_price_wrap{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% - 40px);
}

.tb_rtp_d_price {
    opacity: .5;
    text-decoration: line-through;
    transform: scale(0.85);
}

.tb_rtp_dp{
    margin-right: 5px;
    color: green;
    transform: scale(0.85);
    font-weight: 600;
}
.tb_rtp_time{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    opacity: .5;
    font-size: 14px;
    font-family: var(--tb-price-font);
    color: var(--tb-price-color);
}

.tb_rtp_price, .tb_rtp_d_price{
    font-family: var(--tb-price-font);
    font-size: var(--tb-price-size);
    color: var(--tb-price-color);
    font-weight: var(--tb-price-weight);
    font-style: var(--tb-price-style);
    margin-right: 5px;
}

.tb_rtn_wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: -6px;
    z-index: 1;
    bottom: 0;
    background-color: var(--tb-bg-color);
}

.tb_rtn_wrap.tb_rtn_wrap_disabled{
    display: none;
}

.tb_rtn_btn{
    margin: 2px 5px;
}

.swiper-button-disabled.tb_rtn_btn{
    opacity: .3;
    pointer-events: none;
}

.tb_rtn_btn{
    font-size: 14px;
    color: #000;
    padding: 3px;
}